<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>市场管理</BreadcrumbItem>
      <BreadcrumbItem>添加货种</BreadcrumbItem>
    </Breadcrumb>
     <div class="box-container">
       <Collapse v-model="value1">
            <Panel name="1">
                货种基本信息
                <Row slot="content">
                   <Col span="1">货种</Col>
                   <Col span="20">
                      <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
                          <FormItem prop="type">
                              <Input  v-model="formInline.type" placeholder="请输入货种种类" style="width:220px"></Input>
                          </FormItem>
                          <FormItem prop="cargo">
                              <Input  v-model="formInline.cargo" placeholder="请输入货种名称" style="width:220px"></Input>
                          </FormItem>
                          <FormItem>
                              <Button type="primary" @click="handleSubmit('formInline',1)">添加</Button>
                          </FormItem>
                      </Form>
                  </Col>
                </Row>
                 <Row slot="content"  >
                    <table class="table table-row">
                        <tr>
                            <td>货种种类</td>
                            <td>货种名称</td>
                            <td>操作</td>
                        </tr>
                        <tr v-for="(item,index) in cargoList " :key='index'>
                            <td v-if="!item.isEditor">{{item.type}}</td>
                            <td v-if="!item.isEditor">{{item.cargo}}</td>
                            <td v-if="item.isEditor"> <Input style="width:120px" placeholder="请输入货种种类" v-model="item.type" /> </td>
                            <td v-if="item.isEditor"><Input style="width:120px" placeholder="请输入货种名称"   v-model="item.cargo" /> </td>
                            <td>
                              <Button type="primary" size='small' v-if="!item.isEditor" @click="editor(index)">修改</Button>
                              <Button type="primary" size='small' @click="save(item.type,item.cargo,item.code,index)"  v-else >保存</Button>
                              <Button type="primary" size='small' @click="cancel(index)"  v-if="item.isEditor" >取消</Button>
                              <i-switch v-model=" item.isOpen " @on-change="changeSwitch($event,item.code,index)" >
                                <span slot="open">启用</span>
                                <span slot="close">禁用</span>
                               </i-switch>
                            </td>
                        </tr>
                    </table>
                  </Row>
            </Panel>
        </Collapse>
     </div>
  </Card>
</template>
<script>
export default {
  data(){
    return {
      value1:"1",
      switch1:true,
      cargoList:[],
      cargoInfo:{
        type:'',
        cargo:""
      },
    formInline: {
              type: '',
              cargo: ''
          },
    ruleInline: {
        type: [
            { required: true, message: '请输入货种种类', trigger: 'blur' }
        ],
        cargo: [
            { required: true, message: '请输入货种名称', trigger: 'blur' },

        ]
    },
   }
  },
  methods:{
    // 添加修改货种
      handleSubmit(name) {
        this.$refs[name].validate(async (valid) => {
            if (valid) {
               let res = await this.$http.AddCargo({ cargoType:this.formInline.type,cargo:this.formInline.cargo,code:'',})
               if(!res.data.status){
                 this.$Notice.info({
                   desc:res.data.msg
                 })
                 this.formInline.type = ''
                 this.formInline.cargo = ''
                 this.getAllCargoType()
               }
            } else {
                // this.$Message.error('Fail!');
            }
        })
    },
    // 修改货种
    editor(i){
      this.cargoList[i].isEditor = true
    },
    cancel(i){
       this.cargoList[i].isEditor = false
    },
   async save(cargoType,cargo,code,i){
       let res = await this.$http.AddCargo({ cargoType,cargo,code,})
        if(!res.data.status){
          this.cargoList[i].isEditor = false
          this.getAllCargoType()
        }
        this.$Notice.info({
          desc:res.data.msg
        })
    },
    // 货种列表
   async getAllCargoType(){
      let res = await this.$http.getAllCargoType()
      this.cargoList = []
      if(!res.data.status){
          let result = res.data.result
           result.map((item,index)=>{
                  this.cargoList.push(
                      Object.assign({},item,{
                        isEditor:false,
                        isOpen:item.status ==1 ? true : false
                      })
                  )
            })
      }
      else{
        this.cargoList = []
      }
    },
    // 启用禁用货种
   async changeSwitch(status,cargoCode,index){
     // return
        let res = await this.$http.updateCargoType({cargoCode,status:status?1:0})

        if(!res.data.status && status){
          this.$Notice.success({
            desc:'开启成功'
          })
          this.getAllCargoType()

        }
        else  if(!res.data.status && !status){
          this.$Notice.success({
            desc:'禁用成功'
          })
          this.getAllCargoType()
        }
        else{
          this.$Notice.error({
            desc:'操作成功'
          })
        }
    },
  },
  created(){
    this.getAllCargoType()
  }
}
</script>

<style lang="scss" scoped>
  .table, tr ,td{
      border: 1px solid #e4e4ee;
      border-collapse: collapse;
      padding: 8px 30px;
      text-align: center;
      margin: 20px 55px;
    }
</style>



